$light-yellow-color: #F3B312;
$darken-yellow-color: #FC9700;

@font-face {
  font-family: "Muller-regular";
  src: url('../fonts/Muller-Trial-Regular.woff2') format("woff2"),
  url('../fonts/Muller-Trial-Regular.woff') format("woff");
}

.body {
  box-sizing: border-box;
  overflow: hidden;

}

.main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  font-family: "Muller-regular", sans-serif;

  .main-wrapper {
    width: 700px;
  }

  &__title {
    margin-bottom: 15px;
    font-weight: 300;
    font-size: 30px;
    letter-spacing: 0.2px;
    transform: translate(22px);

    p {
      color: red;
      font-weight: 700;
    }
  }
}

.main-list {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
  width: 100%;
  list-style: none;
  transform: translate(17px);

  &__item {
    display: flex;
    align-items: center;
    font-size: 15px;

    span {
      color: $light-yellow-color;
    }

    img {
      margin-right: 15px;
    }
  }

  .present {
    margin-right: 35px;

    img {
      width: 40px;
    }
  }

  .time {
    margin-right: 33px;

    img {
      width: 36px;
    }
  }

  .cost {
    img {
      width: 30px;
    }
  }
}

