// content that i hide on click
.content-form,
.content {
  //display: none;
  max-width: 345px;
  position: absolute;
  top: 25px;
  left: 110px;

  .title {
    font-weight: 400;
    white-space: nowrap;
  }

  &__list {
    margin-top: 35px;
    display: grid;
    grid-template-columns: repeat(3, 20%);
    grid-row-gap: 25px;
    grid-column-gap: 70px;
    align-items: end;
    padding: 0;
  }

  &__list-item {
    cursor: pointer;

    img {
      &:hover {
        transform: scale(1.1);
      }
    }

    span {
      &:hover {
        transform: scale(1.1);
      }
    }

    .kia {
      width: 73px;
    }

    .mazda {
      width: 54px;
    }

    .volvo {
      width: 54px;
    }

    .ford {
      width: 80px;
    }

    .volkswagen {
      width: 54px;
    }

    .renault {
      width: 50px;
    }

    .hyundai {
      width: 70px;
    }

    .mitsubishi {
      width: 56px;
    }

    .inner-mark {
      display: flex;
      flex-direction: column;
      align-items: center;
      font-size: 18px;
    }
  }
}

.bottom-text {
  display: block;
  text-align: center;
  font-size: 14px;
  transform: translate(-50px);
}

//second screen
.content-form {
  display: none;

  &__inner {

  }

  &__title {
    font-weight: 400;
    letter-spacing: 0.3px;
    margin-bottom: 20px;
  }

  &__input {
    position: relative;
    display: block;
    padding: 0 10px;
    width: 100%;
    height: 38px;
    border: 2px solid $darken-yellow-color;
    border-radius: 4px;
    margin-bottom: 20px;

    &:not(:focus) + .content-form__input-cursor:before {
      display: inline-block;
      animation: cursor 1s infinite step-end;
    }
  }

  @keyframes cursor {
    50% {
      background: transparent;
    }
  }

  &__input-cursor {
    position: relative;
    &:before {
      content: '';
      display: none;
      position: absolute;
      left: 10px;
      top: -48px;
      width: 1px;
      height: 17px;
      background: #000;
      opacity: 1;
      z-index: 3;
    }
  }

  //Checkbox-list
  &__checkbox-list {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    gap: 10px;
  }

  &__checkbox-list-item {
    display: flex;
    align-items: center;
  }

  &__checkbox-list-label {
    cursor: pointer;
  }

  &__checkbox {
    appearance: none;
    width: 20px;
    height: 20px;
    margin-right: 5px;
    background: url(../img/empty-checkbox.svg) no-repeat;
    background-size: 20px 20px;

    &:checked {
      background: url(../img/checked-checkbox.svg) no-repeat;
      background-size: 20px 20px;
    }
  }

  //  buttons
  &__footer {
    display: flex;
  }

  &__footer-button {
    width: 200px;
    height: 38px;
    margin-right: 10px;
    border-radius: 4px;
    background-color: $darken-yellow-color;
    color: white;
    font-weight: 300;
    font-size: 14px;
    cursor: pointer;

    &:hover {
      font-weight: 700;
      background-color: red;
    }

    &:last-child {
      margin-right: 0;
    }

  }
}

.content-form__inner-success {
  .content-form__title {
    letter-spacing: 0.4px;
    margin-bottom: 30px;
  }
  .content-form__input-wrapper{
    margin-bottom: 30px;
  }
  .content-form__checkbox {
    width: 35px;
    height: 20px;
    margin-right: 5px;
  }
  .content-form__checkbox-list-label{
    font-size: 12px;
  }
}

//Four-page
.content-form__thanks-page {
  position: absolute;
  left: 50px;
  top: 80px;
  .content-form__title {
    width: 320px;
  }
}
// body line
.body-line {
  position: absolute;
  bottom: 55px;
  left: 120px;
  width: 240px;
  height: 18px;
  border-radius: 2px;
  border: 3px solid $darken-yellow-color;

  &__indicator {
    height: 100%;
    width: 33.333%;
    background-color: red;
    margin-bottom: 5px;
  }
}

.body-line__text {
  transform: translate(5px, -30px);

  p {
    color: red;
    font-size: 17.5px;
    white-space: nowrap;

    span {
      font-size: 50px;
      font-weight: 700;
    }
  }
}
